.inputHolder{
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.input{
  font-size: 20px;
  width: 400px;
 border: 1px solid gray;
 padding: 10px 15px;
 border-top-left-radius: 10px;
 border-bottom-left-radius: 10px;
 cursor: pointer;
 outline: none;
}
.icon{
  border: 1px solid gray;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  color: aliceblue;
  background-color: royalblue;
  cursor: pointer;
  width: 30px;
  height: 30px;
  padding: 6.5px;
}